<template>
  <el-form ref="AccountFrom" :model="account" :rules="rules" label-position="left" label-width="0px"
           class="demo-ruleForm login-container">
    <!-- <a href="/adminlogin"><img src="../assets/heist.png"></a> -->
    <h3 class="title">Laundry查询系统登录</h3>
    <el-form-item prop="username">
      <el-input type="text" v-model="account.username" auto-complete="off" placeholder="账号"></el-input>
    </el-form-item>
    <el-form-item prop="password">
      <el-input type="password" v-model="account.password" auto-complete="off" placeholder="密码"></el-input>
    </el-form-item>
    <el-checkbox v-model="checked" checked class="remember">记住密码</el-checkbox>
    <el-form-item style="width:100%;">
      <el-button type="primary" style="width:100%;" @click.native.prevent="handleLogin" :loading="loading">登录</el-button>
    </el-form-item>
  </el-form>
</template>

<script>
  import API from '../api/api_user';

  export default {
    data() {
      return {
        loading: false,
        account: {
          username: '',
          password: ''
        },
        rules: {
          username: [
            {required: true, message: '请输入账号', trigger: 'blur'},
            //{ validator: validaePass }
          ],
          password: [
            {required: true, message: '请输入密码', trigger: 'blur'},
            //{ validator: validaePass2 }
          ]
        },
        checked: true
      };
    },
    methods: {
      handleLogin() {
        let that = this;
        this.$refs.AccountFrom.validate((valid) => {
          if (valid) {
            this.loading = true;
            let loginParams = {username: this.account.username, password: this.account.password};
            API.login(loginParams).then(function (result) {
              that.loading = false;
              if (result && result.access_token) {
                //localStorage.setItem('access-user', JSON.stringify(result));
                sessionStorage.setItem('access-user', JSON.stringify(loginParams.username));
                sessionStorage.setItem('token', result.access_token);
                
                //sessionStorage.setItem('token', token);

//                that.$store.commit('SET_ROUTERS', user.permissions)
//                that.$router.addRoutes(that.$store.getters.addRouters);
//                that.$router.options.routes = that.$store.getters.routers;             
                let loginUser = JSON.parse(window.sessionStorage.getItem('access-user'));
                API.getPrivilege(loginUser).then(function (result) {
                  if (result && result.errcode==0) {
                     console.log("get privilege");
                     console.log(result.privilege);
                     sessionStorage.setItem('privilege', result.privilege);
                     that.$router.push({path: '/auth/dashboard'});
                  }
                }, function (err) {
                  console.log("cannot get user privilege");
                  console.log(err);
                }).catch(function (error) {
                  console.log("cannot get user privilege");
                  console.log(error);
                });        
        
                // 把跳转到主页的操作放到getPrivilege的回调中，要不然由于异步执行，在加载主页面的时候 privilege还没获取，
                //that.$router.push({path: '/'});
              } else {
                that.$message.error({showClose: true, message: result.errmsg || '登录失败', duration: 2000});
              }
            }, function (err) {
              that.loading = false;
              let errStr = err.toLocaleString();
              let msg = '';
              console.log(errStr);
              if (errStr.indexOf('401') >= 0){
                msg = "用户名和密码不对，请重试";
              }
              else if(errStr.indexOf('Network')>= 0){
                msg = "网络连接异常，请检查网络或稍后再试";
              }
              else
                msg = errStr;
              that.$message({ message: msg,  type: 'error', duration:5500});
              //that.$message.error({showClose: true, message: err.toString(), duration: 5000});
            }).catch(function (error) {
              that.loading = false;
              console.log(error);
              that.$message.error({showClose: true, message: '请求出现异常', duration: 2000});
            });
          }
        });
      }
    }
  }
</script>
<style>
  body {
    background: #DFE9FB;
  }
</style>
<style lang="scss" scoped>
  .login-container {
    /*box-shadow: 0 0px 8px 0 rgba(0, 0, 0, 0.06), 0 1px 0px 0 rgba(0, 0, 0, 0.02);*/
    -webkit-border-radius: 5px;
    border-radius: 5px;
    -moz-border-radius: 5px;
    background-clip: padding-box;
    margin: 160px auto;
    width: 350px;
    padding: 35px 35px 15px 35px;
    background: #fff;
    border: 1px solid #eaeaea;
    box-shadow: 0 0 25px #cac6c6;

    background: -ms-linear-gradient(top, #ace, #00C1DE); /* IE 10 */
    background: -moz-linear-gradient(top, #ace, #00C1DE); /*火狐*/
    background: -webkit-gradient(linear, 0% 0%, 0% 100%, from(#ace), to(#00C1DE)); /*谷歌*/
    background: -webkit-linear-gradient(top, #ace, #00C1DE); /*Safari5.1 Chrome 10+*/
    background: -o-linear-gradient(top,#ace, #00C1DE); /*Opera 11.10+*/

    .title {
      margin: 0px auto 40px auto;
      text-align: center;
      color: #505458;
    }
    .remember {
      margin: 0px 0px 35px 0px;
    }
  }
</style>



